import React from 'react';
import PropTypes from 'prop-types';
import { Navigate } from 'react-router';
import About from './About';
import { Link } from "react-router-dom";


const Navbar = (props) => {
 
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
        <div className="container-fluid">
          <Link className="navbar-brand" to="/textform">{props.title}</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item" > 
                <Link className="nav-link " aria-current="page" to="/textform" >Text Form</Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link " to="/about" >About</Link>
              </li>
             
              <li className="nav-item dropdown">
                <Link className="nav-link dropdown-toggle" to="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  Dropdown
                </Link>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li><Link className="dropdown-item" to="#">Action</Link></li>
                  <li><Link className="dropdown-item" to="#">Another action</Link></li>
                  <li><hr className="dropdown-divider"/></li>
                  <li><Link className="dropdown-item" to="#">Something else here</Link></li>
                </ul>
              </li>
            </ul>
            <div className="form-check form-switch mx-5">
                  <input className="form-check-input" onClick={props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckChecked"/>
                  <label className="form-check-label" for="flexSwitchCheckChecked" style={{color : props.mode=="dark"?"white":"black"}}>Dark Mode</label>
            </div>
            <form className="d-flex">
              <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-outline-primary" type="submit">Search</button>
            </form>

          </div>
        </div>
      </nav>        
    );
};

export default Navbar;

Navbar.defaultProps={
  title:'Set title here',
}

Navbar.propTypes={
   title:PropTypes.string.isRequired,

}