import logo from './logo.svg';
import { useState } from 'react';
import {
  BrowserRouter,
  Routes,
  Route
} from "react-router-dom";
import './App.css';
import Navbar from './components/Navbar';
import TextForm from "./components/TextForm";
import About from './components/About';
import Alert from './components/Alert';

let name= "harsh";


function App() {
  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);

  const showAlert=(message,type)=>{
    setAlert({
      msg:message,
      type:type
    });
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }
  const toggleMode=()=>{
    if(mode==="light"){
      setMode("dark");
      document.body.style.backgroundColor = "black";
      document.body.style.color = "white";
      showAlert("dark mode enabled","success");
      document.title = "darker";

    }else{
      setMode("light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";
      showAlert("light mode enabled","success");
      document.title = "lighter";
      
    }
  };

  return (
    <>
    <BrowserRouter>
      <Navbar title="utilz" mode={mode} toggleMode={toggleMode} />
      <div className="container my-3">
      <Alert alert={alert}/>
      <Routes>
     <Route
      path="*"
      element={
        <main style={{ padding: "1rem" }}>
          <p>There's nothing here!</p>
        </main>
      }/>
          <Route path="/about" element={<About mode={mode} toggleMode={toggleMode}/>}></Route>
          <Route path="/textform" element={<TextForm showAlert={showAlert} />}></Route>
      </Routes>
     
      {/* <TextForm showAlert={showAlert} />
      <About mode={mode} toggleMode={toggleMode}/> */}
      </div>
   </BrowserRouter>
    </>
  );
}

export default App;
