import React,{useState} from 'react'
export default function TextForm(props) {
    const handleUpClick=()=>{
        // console.log("uppercase was clicked");
        setText("new text");
        setText(text.toUpperCase());
        props.showAlert("Text converted to uppercase","success");
       
    }
    const handleOnChange=(event)=>{
        // console.log("Onchange");
        setText(event.target.value);
       
    }
    const handleClear=(event)=>{
        // console.log("Clear");
        setText("");
        props.showAlert("Text cleared","success");
       
    }
    const [text,setText] = useState("Enter text here");
    
    return (
    <>
            <div className="container">
                <div className="mb-3">
                    <label htmlFor="textbox" className="form-label">Example textarea</label>
                    <textarea className="form-control" value={text} id="textbox" rows="3" onChange={handleOnChange}></textarea>
                </div>
                <button className="btn btn-primary mx-3" onClick={handleUpClick}>Convert Uppercaase</button>
                <button className="btn btn-primary mx-3" onClick={handleClear}>Clear Textbox</button>
            </div>

            <div className="container my-3" >
                <h1>Your text summary</h1>
                <p>{text.split(/\s+/).length} words,{text.length} characters</p>
                <h2>Preview</h2>
                <p>{text}</p>
            </div>
     </>
    ) 
}
// style={{color:props.mode==="dark"? "white":"black"}}